  border-radius: 0px 0px 10px 10px;
  background-color: none;
  color: #FFFFFF;
  z-index:84;
  width: 100px;
  height: 10px;
  text-align: center;
  display: inline-block;
  position: absolute;
  right: 0;
  left: 0;
  top: 40px;
  margin:auto;
}

.motoLogo {
  padding: 20px 0px 0px 0px;
  border-radius: 0px 0px 10px 10px;
  background-color: none;
  color: #FFFFFF;
  z-index:84;
  width: 300px;
  height: 10px;
  text-align: center;
  display: inline-block;
  position:absolute;
  left:0;
  right:0;
  top:20;
