}

.leftTop {
  padding: 0;
  border-radius: 0;
  background-color: none;
  color: #FFFFFF;
  z-index:81;
  width: 150px;
  height: 20px;
  
  float:Left;
  margin:0;
}

.rightTop {
  padding: 0;
  border-radius: 0;
  background-color: none;
  color: #FFFFFF;
  z-index:81;
  width: 150px;
  height: 20px;
  
  float:left;
  margin:0;
