.inclimate {
  padding: 5px 5px 5px 5px;
  border-radius: 0px;
  border-style: solid;
  border-color: rgb(78, 20, 20);
  border-width: 0px;
  background: none;
  color: #FFFFFF;
  width: 100%;
  height: 290px;
  text-align: center;

  position:relative;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin-top: 0px;
  margin-bottom: 0px;
  
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input.text { 
   background-color:#FFFFFF;
   border:solid 0px #A9A9A9;
   font-size:18px;
   color:#000000;
   -moz-border-radius:5px;
   -webkit-border-radius:5px;
   border-radius:5px;
   padding-top:5px;
   padding-bottom:5px;
   padding-left:5px;
   padding-right:5px;
   width: 97%;
}

.button {
  background-color: rgb(0, 0, 0);
  border: none;
  color: white;
  padding: 15px 32px;
  border-radius: 0px 0px 0px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  margin: 4px 2px;
  width: 100%;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}

.buttonClose {
  background-color: rgb(0, 0, 0);
  border: none;
  color: white;
  padding: 5px 5px;
  border-radius: 5px 5px 5px 5px;
  text-align: center;
  text-decoration: none;
  font-size: 17px;
  margin: 0;
  width: 30px;
  height: 30px;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}

.buttonL {
  background-color: rgb(0, 0, 0);
  border: none;
  color: white;
  padding: 10px 10px;
  border-radius: 0px 0px 0px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 17px;
  margin: 0;
  width: 150px;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}

.buttonR {
  background-color: rgb(65, 7, 7);
  border: none;
  color: white;
  padding: 10px 10px;
  border-radius: 0px 0px 10px 0px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 17px;
  margin: 0;
  width: 150px;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}


.button:hover {
  background-color: rgb(20, 57, 87);
}

.buttonClose:hover {
  background-color: rgb(148, 35, 0);
}

.buttonL:hover {
  background-color: rgb(20, 57, 87);
}

.buttonR:hover {
  background-color: rgb(20, 57, 87);
}

.collapsible {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .collapsible:hover {
  background-color: #ccc;
}

/* Style the collapsible content. Note: hidden by default */
.content {
  padding: 0 18px;
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
}
